export default {
	data() {
		return {
			startTime:'',
			endTime:'',
			currentSelect:'',
			dataArr: [
				{
					name: '腾讯课堂',
					value: '1',
					logoUrl: '/static/mp-weixin/tencent.png',
					courseName:'计算机原理',
					loginTime:'3月3日 9:00',
					learningTime:'9:00-10:00',
				},
				{
					name: '钉钉',
					value: '2',
					logoUrl: '/static/mp-weixin/dingding.png',
					courseName:'思想品德修养',
					loginTime:'3月4日 9:00',
					learningTime:'9:00-10:00',
				},
				{
					name: '凤凰职教云',
					value: '3',
					logoUrl: '/static/mp-weixin/fenghuang.png',
					courseName:'计算机原理',
					loginTime:'3月5日 9:00',
					learningTime:'9:00-10:00',
				},
				{
					name: '智慧职教',
					value: '4',
					logoUrl: '/static/mp-weixin/zhihui.png',
					courseName:'数学',
					loginTime:'3月3日 9:00',
					learningTime:'9:00-10:00',
				},
				{
					name: '超星泛雅',
					value: '5',
					logoUrl: '/static/mp-weixin/chaoxing.png',
					courseName:'体育',
					loginTime:'3月4日 9:00',
					learningTime:'9:00-10:00',
				}
			]
		};
	},
	mounted(){
		this.initData()
	},
	methods: {
		handleChooseTime(isStartTime){
			if(isStartTime){
				this.currentSelect = 'startTime'
			}else{
				this.currentSelect = 'endTime'
			}
			this.$refs.linkage.show()
		},
		handleCancelChoose(){
			this.$refs.linkage.hide()
		},
		handleChoose({checkArr,checkValue,defaultVal,result}){
			console.log(arguments,'arguments')
			this[this.currentSelect] = result;
			this.$refs.linkage.hide()
		},
		initData(){
			this.$HTTP({
				url:'/statistical/getXxqk',
				params:{
					kssj:this.startTime,
					jssj:this.endTime,
					userid:uni.getStorageSync('userId')
				},
				successCallback:({data})=>{
					if(data.code == 0){
						console.log(data,'获取参数')
					}else{
						uni.showToast({
							title:data.msg,
							icon:'none'
						})
					}
					
				}
			})
		},
		handleNav() {
			uni.showToast({
				title: '暂未开通',
				icon: 'none'
			});
		}
	}
};
